import React from 'react';

//imports
import { Grommet } from 'grommet';
import { Routes, Route, Outlet, Link } from "react-router-dom";
import background from './assets/background_home.jpg'
//screens

// import { TicketList } from './screens/ticketslist/ticketlist'
// import { LoginSceen } from './screens/login/login'

import LoginSceen from './screens/login/login';
import HomeScreen from './screens/home/home';

function App() {
  const [logando, setLogado] = React.useState(true);
  const [user, setUser] = React.useState([]);
  const theme = {
    global: {
      font: {
        family: 'Poppins',
        size: '10px',
        height: '20px',
      },
    },
  };



  return (
    <Grommet theme={theme}>

      {logando ? (<LoginSceen handleChange={() => setLogado(false)} setUser={(data) => setUser(data)} />) : (<HomeScreen handleChange={() => setLogado(true)}  setUser={(data) => setUser(data)}/>)
      }

    </Grommet >
  );

}

export default App;
