import React from 'react';
import { Main, Box, Image, Button, Nav, Header, Card, CardHeader, CardBody, Select, Text, TextInput, } from 'grommet';
import background from '../../assets/background_home.png'
import Avatar from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/material/styles';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import backmid from '../../assets/backgroundmiddle.jpg'
import logobus from '../../assets/logobusticket.png';
import QueryBuilderIcon from '@mui/icons-material/QueryBuilder';
import { Link } from "react-router-dom";
import api from '../../services/api';
import Popover from '@mui/material/Popover';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import ContentPasteIcon from '@mui/icons-material/ContentPaste';
import CheckIcon from '@mui/icons-material/Check';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import { BrowserView, MobileView, isBrowser, isMobile } from 'react-device-detect';
export const HomeScreen = ({ handleChange }) => {


    const BoxHome = [
        {
            color: 'transparent',
            size: "15vh",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "15vh",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];

    const BoxHomeCelular = [
        {
            color: 'transparent',
            size: "5vh",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "5vh",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];

    const styleBackGroundHome = {
        backgroundImage: `url(${background})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center'

    }

    const styles = {
        center: {
            display: 'flex', justifyContent: 'center', alignItems: 'center', backgroundImage: `url(${backmid})`
        },
        centerLogo: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        prefooter: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        buttonTicket: {
            width: '13vh', height: '8vh', backgroundColor: '#0c73df', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketCancelar: {
            width: '12vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketQuantidade: {
            height: '4.5vh', width: '40%', backgroundColor: '#0c73df', color: 'black', borderColor: 'black', border: '2px', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketCelular: {
            width: '25vh', height: '8vh', backgroundColor: '#0c73df', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center', display: 'flex', alignItems: 'center', justifyContent: 'center',
        },
        buttonTicketCancelarCelular: {
            width: '25vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
    }

    const [origem, setOrigem] = React.useState('Toncontin')
    const [destino, setDestino] = React.useState('Palmerola')
    const [quantidade, setQuantidade] = React.useState(1);
    const [valor, setValor] = React.useState(200);
    const [horarioIda, setHorarioida] = React.useState('07:00')
    // const [horarioChegada, setHorarioChegada] = React.useState('08:30')
    const [nome, setNome] = React.useState('')
    const [sobrenome, setSobrenome] = React.useState('')
    const [documento, setDocumento] = React.useState('')
    const [user, setUser] = React.useState([])
    const [loading, setLoading] = React.useState(true)
    const [quantidadeVendas, setQtdVendas] = React.useState(0);
    const [quantidadeVendasTarjetas, setQtdVendasTarjetas] = React.useState(0);
    const [valorVendas, setVlrVendas] = React.useState(0)
    const [valorEfectivo, setVlrEfectivo] = React.useState(0)
    const [valorTarjeta, setVlrTarjeta] = React.useState(0)
    const [quantidadeTotal, setQtdTotal] = React.useState(0)

    const horarioReduzidos = ['07:00', '08:30', '14:30', '16:00'];
    const horarioToc = ['07:00', '08:30'];
    const horarioPal = ['14:30', '16:00'];
    const [pagando, setPagando] = React.useState(false);
    const horarios = [
        '06:00',
        '06:30',
        '07:00',
        '07:30',
        '08:00',
        '08:30',
        '09:00',
        '09:30',
        '10:00',
        '10:30',
        '11:00',
        '11:30',
        '12:00',
        '12:30',
        '13:00',
        '13:30',
        '14:00',
        '14:30',
        '15:00',
        '15:30',
        '16:00',
        '16:30',
        '17:00',
        '17:30',
        '18:00',
        '18:30',
        '19:00',
        '19:30',
        '20:00',
        '20:30',
        '21:00',
        '21:30',
        '22:00',
        '22:30',
        '23:00',
        '23:30',
        '00:00',
        '00:30'
    ];

    const handleCancelar = () => {
        setNome('');
        setSobrenome('');
        setDocumento('');
        setQuantidade(1);
        setPagando(false)
    }

    React.useEffect(() => {
        let token = localStorage.getItem("token");

        let config = {
            headers: {
                Authorization: 'Bearer ' + token
            }
        }

        let data = {

        }

        api.post('/me', data, config).then((response) => {
            console.log(response.data);
            if (response.data.message === 'sucess') {

                setUser(response.data.usuario.original)
                setLoading(false)
                return 1;
            }
            else {
                localStorage.clear();

            }

        })
            .catch((error) => console.log(error))
    }, [])

    function stringToColor(string) {
        let hash = 0;
        let i;

        /* eslint-disable no-bitwise */
        for (i = 0; i < string.length; i += 1) {
            hash = string.charCodeAt(i) + ((hash << 5) - hash);
        }

        let color = '#';

        for (i = 0; i < 3; i += 1) {
            const value = (hash >> (i * 8)) & 0xff;
            color += `00${value.toString(16)}`.substr(-2);
        }
        /* eslint-enable no-bitwise */

        return color;
    }

    function stringAvatar(name) {
        return {
            sx: {
                bgcolor: stringToColor(name),
            },
            children: `${name.split(' ')[0][0]}${name.split(' ')[1][0]}`,
        };
    }

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;


    const handlePagamento = (forma_pago) => {
        console.log(origem, destino, quantidade, valor, nome, sobrenome, documento);
        var data = new Date();
        var dia = String(data.getDate()).padStart(2, '0');
        var mes = String(data.getMonth() + 1).padStart(2, '0');
        var ano = String(data.getFullYear().toString().substr(-2));
        var horas = String(data.getHours()).padStart(2, '0');
        var minutos = String(data.getMinutes()).padStart(2, '0');
        console.log(ano);
        if (origem === 'Toncontin') {
            var punto_emission = '002 de Toncontin a Palmerola';
            var correlativo_autorizado_desde = '001-002-01-00000001';
            var correlativo_autorizado_hasta = '001-002-01-00020000';
            var correlativo_factura_origen = 2;
            if (horarioIdaAtual === '07:00') {
                var motorista = 'CONDUCTOR 1'
            }
            else {
                var motorista = 'CONDUCTOR 2'
            }

        }
        else {
            var punto_emission = '003 de Palmerola a Toncontin';
            var correlativo_autorizado_desde = '001-003-01-00000001';
            var correlativo_autorizado_hasta = '001-003-01-00020000';
            var correlativo_factura_origen = 3;
            if (horarioIdaAtual === '14:30') {
                var motorista = 'CONDUCTOR 3'
            }
            else {
                var motorista = 'CONDUCTOR 4'
            }
        }

        var fecha = dia + '/' + mes + '/' + ano;
        var hora = horas + ":" + minutos;
        var vendedor = user.nome;
        var cliente = nome + ' ' + sobrenome;
        var doc = documento;
        var precio = valor;
        var descuento = 0;
        var importe_gravado = 0;
        var importe_exento = valor;
        var isv = 0;
        var total = valor;
        let user_id = user.id;



        api.post('ticket-create', {
            punto_emission: punto_emission,
            correlativo_autorizado_desde: correlativo_autorizado_desde,
            correlativo_autorizado_hasta: correlativo_autorizado_hasta,
            correlativo_factura_origen: correlativo_factura_origen,
            fecha: fecha,
            hora_emission: hora,
            hora: horarioIdaAtual,
            vendedor: vendedor,
            cliente: cliente,
            documento: doc,
            precio: precio,
            descuento: descuento,
            importe_gravado: importe_gravado,
            importe_exento: importe_exento,
            isv: isv,
            total: total,
            user_id: user_id,
            forma_pago: forma_pago,
            cantidad: quantidade,
            condutor: motorista,
            origen: origem,
            destino: destino,
        }).then((response) => {
            console.log(response.data)
            if (isBrowser) {
                window.open('http://192.168.1.103:80/api/ticket-get/' + response.data.id, "_blank");
            }
            else {
                window.open('http://192.168.1.103:80/api/ticket-get/' + response.data.id);
            }


            if (forma_pago == 'EFECTIVO') {
                setQtdVendas(quantidadeVendas + quantidade);
                setVlrEfectivo(valorEfectivo + quantidade * 200);
            }
            else {
                setVlrTarjeta(valorTarjeta + quantidade * 200);
                setQtdVendasTarjetas(quantidadeVendasTarjetas + quantidade);
            }

            setQtdTotal(quantidadeTotal + quantidade);
            setVlrVendas(valorVendas + valor);

            setPagando(false)
            setQuantidade(1);
            setNome('');
            setSobrenome('');
            setDocumento('');
            setValor(200)
            setOpen(false);
        })




    }
    const [openModal, setOpen] = React.useState(false);

    const [horariosSelect, setHorariosSelect] = React.useState(['07:00', '08:30']);
    const [horarioIdaAtual, setHorarioidaAtual] = React.useState('07:00');

    const handleChangeOrigen = (option) => {
        if (option === 'Toncontin') {
            setHorariosSelect(['07:00', '08:30']);
            setHorarioidaAtual('07:00');
        }
        else {
            setHorariosSelect(['14:30', '16:00']);
            setHorarioidaAtual('14:30');
        }
    }
    return (
        <>
            <BrowserView>
                < Main>
                    <Dialog
                        open={openModal}
                        onClose={() => setOpen(false)}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            <p style={{ textAlign: 'center' }}> Seleccione la forma de pago para emitir el ticket</p>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                <Stack direction={'row'} spacing={5} alignContent={'center'} display={'flex'} justifyContent={'center'}>
                                    <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('TARJETA') }} >
                                        <Stack direction={'column'} >
                                            <Text > TARJETA </Text>

                                        </Stack>
                                    </Button>
                                    <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('EFECTIVO') }} >
                                        <Stack direction={'column'} >
                                            <Text > EFECTIVO</Text>
                                        </Stack>
                                    </Button>
                                </Stack>
                            </DialogContentText>
                        </DialogContent>
                    </Dialog>
                    {loading ? (<> </>) : (<Box style={styleBackGroundHome}>
                        <Box border={BoxHome} pad="medium">
                            <Header pad="small">
                                <Image fill={false} style={{ width: '50%' }} src={logobus} />

                                <Stack direction={'row'} spacing={2} alignItems={'center'}>

                                    <Avatar {...stringAvatar(user.nome)} />

                                    <Button onClick={handleClick}>
                                        <KeyboardArrowDownIcon />
                                    </Button>

                                    <Popover
                                        id={id}
                                        open={open}
                                        anchorEl={anchorEl}
                                        onClose={handleClose}
                                        anchorOrigin={{
                                            vertical: 50,
                                            horizontal: -150,
                                        }}
                                    >

                                        <Stack spacing={2} style={{ borderRadius: '50%', width: '30vh' }} >
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}> <ContentPasteIcon /> Ventas </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}>  L. {valorVendas}.00 <CheckIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeTotal} </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}> L. {valorTarjeta}.00 < AttachMoneyIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeVendasTarjetas} </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}>  L. {valorEfectivo}.00 <CreditCardIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeVendas} </Text>


                                            <Button primary style={{ height: '4.5vh', backgroundColor: 'white', color: 'black', borderColor: 'transparent' }} label={'Cerrar sesión'} onClick={() => { localStorage.clear(); handleChange(); }} />
                                        </Stack>
                                    </Popover>

                                </Stack>
                            </Header>

                            {pagando ? (
                                <>

                                    <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh', marginBottom: '45vh' }}  >
                                        <CardHeader>

                                        </CardHeader>
                                        <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>Nombre</Text>
                                                <TextInput
                                                    // placeholder="type here"
                                                    value={nome}
                                                    onChange={event => setNome(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>Apellido</Text>
                                                <TextInput
                                                    // placeholder="type here"
                                                    value={sobrenome}
                                                    onChange={event => setSobrenome(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>RTN / ID</Text>

                                                <TextInput
                                                    // placeholder="type here"
                                                    value={documento}
                                                    onChange={event => setDocumento(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>



                                            <Button primary style={styles.buttonTicket} onClick={() => { setOpen(true) }} >
                                                <Stack direction={'column'} >
                                                    <Text > Siguiente </Text>
                                                    <Text >L. {valor}.00 </Text>
                                                </Stack>
                                            </Button>
                                            <Button primary style={styles.buttonTicketCancelar} onClick={() => handleCancelar()} >
                                                <Stack direction={'column'} >
                                                    <Text > Cancelar </Text>
                                                </Stack>
                                            </Button>
                                        </CardBody>
                                    </Card>
                                </>
                            ) : (
                                <>
                                    <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh', marginBottom: '45vh' }}  >
                                        <CardHeader>

                                        </CardHeader>
                                        <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>Origen</Text>
                                                <Select
                                                    icon={() => (<MyLocationIcon style={{ color: '#fc8332' }} />)}
                                                    options={['Toncontin', 'Palmerola']}
                                                    value={origem}
                                                    onChange={({ option }) => { handleChangeOrigen(option); }}
                                                    size={'small'}

                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>Destino</Text>
                                                <Select
                                                    icon={() => (<LocationOnIcon style={{ color: '#df5a5e' }} />)}
                                                    options={['Toncontin', 'Palmerola']}
                                                    value={destino}
                                                    onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                                    size={'small'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'} >
                                                <Text size='medium'>Horario</Text>
                                                <Stack direction={'column'} width={'100%'}>

                                                    {/* {origem == 'Toncontin' ? ( */}
                                                    <Select
                                                        icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}
                                                        options={horariosSelect}
                                                        value={horarioIdaAtual}

                                                        // options={horarios}
                                                        // value={horarioIda}
                                                        onChange={({ option }) => { setHorarioidaAtual(option) }}
                                                        size={'small'}
                                                    // disabled
                                                    />



                                                    {/* <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}

                                                options={horarioReduzidos}
                                                value={origem == 'Toncontin' ? ('08:30') : ('16:00')}

                                                // options={horarios}
                                                // value={horarioChegada}
                                                // onChange={({ option }) => { setHorarioChegada(option) }}
                                                size={'small'}
                                                disabled
                                            /> */}
                                                </Stack>
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium'>Cantidad</Text>

                                                <Stack direction={'row'} spacing={4}>
                                                    <Button primary style={styles.buttonTicketQuantidade} onClick={() => { if ((quantidade - 1) > 0) { setQuantidade(quantidade - 1); setValor((quantidade - 1) * 200) } }}>
                                                        <Text style={{ textAlign: 'center' }}> -</Text>

                                                    </Button>
                                                    <Button primary style={styles.buttonTicketQuantidade} >
                                                        <Text> {quantidade} </Text>

                                                    </Button>
                                                    <Button primary style={styles.buttonTicketQuantidade} onClick={() => { setQuantidade(quantidade + 1); setValor((quantidade + 1) * 200) }}>
                                                        <Text> + </Text>

                                                    </Button>
                                                </Stack>
                                            </Stack>

                                            <Stack direction={'column'} width={'20%'}>
                                                <Text size='medium' >Total</Text>

                                                <Stack direction={'row'} spacing={4}>
                                                    <Text size='xxlarge'> L. {valor}.00</Text>

                                                </Stack>
                                            </Stack>

                                            <Button primary style={styles.buttonTicket} onClick={() => { setPagando(true) }} >
                                                <Text > Siguiente </Text>

                                            </Button>

                                        </CardBody>
                                    </Card>
                                </>
                            )}

                        </Box>

                    </Box>)}


                </Main >
            </BrowserView>
            <MobileView>
                < Main>
                    <Dialog
                        open={openModal}
                        onClose={() => setOpen(false)}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            <p style={{ textAlign: 'center' }}> Seleccione la forma de pago para emitir el ticket</p>
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                <Stack direction={'row'} spacing={5} alignContent={'center'} display={'flex'} justifyContent={'center'}>
                                    <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('TARJETA') }} >
                                        <Stack direction={'column'} >
                                            <Text > TARJETA </Text>

                                        </Stack>
                                    </Button>
                                    <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('EFECTIVO') }} >
                                        <Stack direction={'column'} >
                                            <Text > EFECTIVO</Text>
                                        </Stack>
                                    </Button>
                                </Stack>
                            </DialogContentText>
                        </DialogContent>
                    </Dialog>
                    {loading ? (<> </>) : (<Box style={styleBackGroundHome}>
                        <Box border={BoxHomeCelular} pad="medium">
                            <Header pad="small">
                                <Image fill={false} style={{ width: '50%' }} src={logobus} />

                                <Stack direction={'row'} spacing={2} alignItems={'center'}>

                                    <Avatar {...stringAvatar(user.nome)} />

                                    <Button onClick={handleClick}>
                                        <KeyboardArrowDownIcon />
                                    </Button>

                                    <Popover
                                        id={id}
                                        open={open}
                                        anchorEl={anchorEl}
                                        onClose={handleClose}
                                        anchorOrigin={{
                                            vertical: 50,
                                            horizontal: -150,
                                        }}
                                    >

                                        <Stack spacing={2} style={{ borderRadius: '50%', width: '30vh' }} >
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}> <ContentPasteIcon /> Ventas </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}>  L. {valorVendas}.00 <CheckIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeTotal} </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}> L. {valorTarjeta}.00 < AttachMoneyIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeVendasTarjetas} </Text>
                                            <Text style={{ textAlign: 'center', fontFamily: 'Poppins', alignItems: 'center', display: 'flex', justifyContent: 'center' }}>  L. {valorEfectivo}.00 <CreditCardIcon sx={{ marginRight: '2vh', marginLeft: '2vh' }} /> {quantidadeVendas} </Text>


                                            <Button primary style={{ height: '4.5vh', backgroundColor: 'white', color: 'black', borderColor: 'transparent' }} label={'Cerrar sesión'} onClick={() => { localStorage.clear(); handleChange(); }} />
                                        </Stack>
                                    </Popover>

                                </Stack>
                            </Header>

                            {pagando ? (
                                <>

                                    <Card height="fill" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '2vh', marginBottom: '18vh' }}  >
                                        <CardHeader>

                                        </CardHeader>
                                        <CardBody pad="medium" direction='column' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>Nombre</Text>
                                                <TextInput
                                                    // placeholder="type here"
                                                    value={nome}
                                                    onChange={event => setNome(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>Apellido</Text>
                                                <TextInput
                                                    // placeholder="type here"
                                                    value={sobrenome}
                                                    onChange={event => setSobrenome(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>RTN / ID</Text>

                                                <TextInput
                                                    // placeholder="type here"
                                                    value={documento}
                                                    onChange={event => setDocumento(event.target.value)}
                                                    size={'medium'}
                                                />
                                            </Stack>
                                            <Stack style={{ display: 'flex', alignItems: 'center' }}>
                                                <Button primary style={styles.buttonTicketCancelarCelular} onClick={() => handleCancelar()} >
                                                    <Stack direction={'column'} >
                                                        <Text > Cancelar </Text>
                                                    </Stack>
                                                </Button>
                                            </Stack>
                                            <Stack style={{ display: 'flex', alignItems: 'center' }}>
                                                <Button primary style={styles.buttonTicketCelular} onClick={() => { setOpen(true) }} >
                                                    <Stack direction={'column'} >
                                                        <Text > Siguiente </Text>
                                                        <Text >L. {valor}.00 </Text>
                                                    </Stack>
                                                </Button>
                                            </Stack>

                                        </CardBody>
                                    </Card>
                                </>
                            ) : (
                                <>
                                    <Card height="fill" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '1vh', marginBottom: '45vh' }}  >
                                        <CardHeader>

                                        </CardHeader>
                                        <CardBody pad="medium" direction='column' gap='large' style={{ marginTop: '3%', position: 'relative' }}>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>Origen</Text>
                                                <Select
                                                    icon={() => (<MyLocationIcon style={{ color: '#fc8332' }} />)}
                                                    options={['Toncontin', 'Palmerola']}
                                                    value={origem}
                                                    onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                                    size={'small'}

                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>Destino</Text>
                                                <Select
                                                    icon={() => (<LocationOnIcon style={{ color: '#df5a5e' }} />)}
                                                    options={['Toncontin', 'Palmerola']}
                                                    value={destino}
                                                    onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                                    size={'small'}
                                                />
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'} >
                                                <Text size='medium'>Horario</Text>
                                                <Stack direction={'column'} width={'100%'}>

                                                    {/* {origem == 'Toncontin' ? ( */}
                                                    <Select
                                                        icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}
                                                        options={horarioReduzidos}
                                                        value={horarioIda}

                                                        // options={horarios}
                                                        // value={horarioIda}
                                                        onChange={({ option }) => { setHorarioida(option) }}
                                                        size={'small'}
                                                    // disabled
                                                    />



                                                    {/* <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}

                                                options={horarioReduzidos}
                                                value={origem == 'Toncontin' ? ('08:30') : ('16:00')}

                                                // options={horarios}
                                                // value={horarioChegada}
                                                // onChange={({ option }) => { setHorarioChegada(option) }}
                                                size={'small'}
                                                disabled
                                            /> */}
                                                </Stack>
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium'>Cantidad</Text>

                                                <Stack direction={'row'} spacing={4}>
                                                    <Button primary style={styles.buttonTicketQuantidade} onClick={() => { if ((quantidade - 1) > 0) { setQuantidade(quantidade - 1); setValor((quantidade - 1) * 200) } }}>
                                                        <Text style={{ textAlign: 'center' }}> -</Text>

                                                    </Button>
                                                    <Button primary style={styles.buttonTicketQuantidade} >
                                                        <Text> {quantidade} </Text>

                                                    </Button>
                                                    <Button primary style={styles.buttonTicketQuantidade} onClick={() => { setQuantidade(quantidade + 1); setValor((quantidade + 1) * 200) }}>
                                                        <Text> + </Text>

                                                    </Button>
                                                </Stack>
                                            </Stack>

                                            <Stack direction={'column'} width={'100%'}>
                                                <Text size='medium' >Total</Text>

                                                <Stack direction={'row'} spacing={4}>
                                                    <Text size='xxlarge'> L. {valor}.00</Text>

                                                </Stack>
                                            </Stack>

                                            <Button primary style={styles.buttonTicketCelular} onClick={() => { setPagando(true) }} >
                                                <Text > Siguiente </Text>

                                            </Button>

                                        </CardBody>
                                    </Card>
                                </>
                            )}

                        </Box>

                    </Box>)}


                </Main >
            </MobileView>
        </>

    )
};

export default HomeScreen;